import React, { Component } from 'react';
import './Employee.css';

class Employee extends Component{
  constructor(props){
    super(props);
    this.state = {name:this.props.name, totalHours: this.props.totalHours};
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(e) {
    this.props.onEmployeeClick(this.state);
  }

  render() {

    let hours = null;
    if(this.state.totalHours>30 && this.state.totalHours<40){
      hours = <span className="employee-hours warn">{this.state.totalHours}</span>
    } else if(this.state.totalHours>40){
      hours = <span className="employee-hours over">{this.state.totalHours}</span>
    } else {
      hours = <span className="employee-hours">{this.state.totalHours}</span>
    }

    return(
      <div className="employee-container" onClick={this.handleClick}>
        <span className="employee-name">
          {this.state.name}
          {hours}
        </span>
      </div>
    );
  }

}

export default Employee;



