import React, { Component } from 'react';
import './EmployeeList.css';
import Employee from '../Employee/Employee';

class EmployeeList extends Component{
  constructor(props){
    super(props);
    this.state = {employees : props.employees};
    this.handleClick = this.handleClick.bind(this);
  }

  handleClick(e){
    this.props.onEmployeeClick(e);
  }

  renderEmployees(employees){
    return employees.map((employee, index) => {
      return <li key={index}><Employee name={employee.name} totalHours={employee.totalHours}  onEmployeeClick={this.handleClick} /></li>
    });
  }

  render(){
    return(
      <div>
        <ul>
          {this.renderEmployees(this.state.employees)}
        </ul>
      </div>
    );
  }



}

export default EmployeeList;



