import React, { Component } from 'react';
import Clock from './components/Clock/Clock';
import Day from './components/Day/Day';
import Timer from './components/Timer/Timer';
import Employee from './components/Employee/Employee';
import EmployeeList from './components/EmployeeList/EmployeeList';
import logo from './logo.svg';
import './App.css';

const employeeData = [
  {name: "Sam Iam", totalHours: "11"},
  {name: "John Doe", totalHours: "43"},
  {name: "Greg Stanton", totalHours: "31"},
  {name: "Roy Jones", totalHours: "0"},
  {name: "Frederick Simpson", totalHours: "49"},
  {name: "Ashley Dawson", totalHours: "36"}];

class App extends Component {
  constructor(props){
    super(props);
    this.handleEmployeeClick = this.handleEmployeeClick.bind(this);
    this.state = {selectedEmployee: {name:null, totalHours:null}};
  }

  handleEmployeeClick(e){
    this.setState({selectedEmployee : {name: e.name, totalHours: e.totalHours}});
  }

  render() {
    return (
      <div className="App">
        <div className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <h2>Sam's React Page</h2>
          <p className="App-intro">
            This is where I practice building React components
          </p>
        </div>
        <div className="component-container">
          <div className="component">
            <h2>An employee model</h2>

            <Employee name="Sam Rodriguez" totalHours="23" onEmployeeClick={this.handleEmployeeClick} />
          </div>
          <div className="component">
            <h2>An employee list with Selected Employee</h2>
            <h3>{this.state.selectedEmployee.name} {this.state.selectedEmployee.totalHours}</h3>
            <EmployeeList employees={employeeData} onEmployeeClick={this.handleEmployeeClick} />
          </div>
          <div className="component">
            <h2>Like a clock!</h2>
            <Clock />
          </div>
          <div className="component">
            <h2>Or a weird timer?</h2>
            <Timer start={new Date()} />
          </div>
          <div className="component">
            <h2>Or a date display, with bonus 'today' highlighting circle.</h2>
            <Day day={1} month={0} />
            <Day day={new Date().getDate()} month={new Date().getMonth()} />
            <Day day={22} month={8} />
            <Day day={25} month={11} />
          </div>
        </div>
      </div>
    );
  }
}

export default App;



