import React, { Component } from 'react';
import Moment from 'react-moment';
import './Clock.css';

class Clock extends Component{
  constructor(props){
    super(props);
    this.state = {date: new Date(), blink: false};
  }

  componentDidMount(){
    this.timerID = setInterval(
      () => this.tick(),
      500
    );
  }

  componentWillUnmount(){
    clearInterval(this.timerID);
  }

  tick(){
    this.setState({
      date: new Date(),
      blink: !this.state.blink
    });
  }

  render() {

    let blinkingSpacer = null;
    if (this.state.blink) {
      blinkingSpacer = <bold className="spacer blink">:</bold>;
    } else {
      blinkingSpacer = <bold className="spacer">:</bold>;
    }
    
    return (
      <div className="clock-container">
        <div className="clock">
          <Moment format="h">{this.state.date}</Moment>
          {blinkingSpacer}
          <Moment format="mm">{this.state.date}</Moment>
          <div className="seconds-am-pm">
            <Moment format="ss">{this.state.date}</Moment>
            <Moment format="a">{this.state.date}</Moment>
          </div>
        </div>
      </div>
    );
  }

}

export default Clock;



