import React, { Component } from 'react';
import './Timer.css';

class Timer extends Component{
  constructor(props){
    super(props);
    this.state = {elapsed: 0 , dots: [], mins: []};
  }

  componentDidMount(){
    this.timerID = setInterval(
      () => this.tick(),
      1000
    );
  }

  componentWillUnmount(){
    clearInterval(this.timerID);
  }

  tick(){
    this.setState({
      elapsed : new Date() - this.props.start,
      dots: this.state.dots.length<9? this.state.dots.concat("-") : [],
      mins: this.state.mins.length>5 ? [] : this.state.dots.length === 9 ? this.state.mins.concat("-") : this.state.mins
    });
  }

  render(){
    // const elapsed = Math.round(this.state.elapsed/100);
    // const seconds = (elapsed/10);
    return(
      <div>
        <div className="dots-container">
          <div className="mins">{this.state.mins}</div>
          <div className="dots">{this.state.dots}</div>
          {/* {seconds} */}
        </div>
      </div>
    );
  }

}

export default Timer;



